// nhng th cn thit
import L from 'leaflet';
import 'leaflet/dist/leaflet.css';
import { GeoSearchControl, OpenStreetMapProvider } from 'leaflet-geosearch';
import 'leaflet-geosearch/dist/geosearch.css';
import 'leaflet-routing-machine';
import 'leaflet-routing-machine/dist/leaflet-routing-machine.css';
import 'leaflet-control-geocoder';
import 'leaflet-control-geocoder/dist/Control.Geocoder.css';
import io from 'socket.io-client'; // Thm import cho socket.io-client

// Khi to kt ni Socket.IO
const socket = io();
// ng dn ti tp icon.SVG ty chnh
import customIconUrl from '/img/person-solid.svg';
import customIconUrl2 from '/img/gps.png';

var map = L.map('map',{zoomControl:false}).setView([10.0488833, 105.7683547], 17); // xa zoomcontrol nu mun xi 2 nt zoom

// Thit lp tile layer t OpenStreetMap
L.tileLayer('https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png', { 
    minZoom: 5,
    maxZoom: 25,
}).addTo(map);

// To biu tng ty chnh t tp SVG
const customIcon = L.icon({
    iconUrl: customIconUrl,
    iconSize: [40, 40], // Kch thc icon (tu chnh)
    iconAnchor: [20, 40], // im neo (gia y icon)
    popupAnchor: [0, -40] // V tr ca popup tng ng vi icon
});

const customGPS = L.icon({
    iconUrl: customIconUrl2,
    iconSize: [40, 40], // Kch thc icon (tu chnh)
    iconAnchor: [20, 40], // im neo (gia y icon)
    popupAnchor: [0, -40] // V tr ca popup tng ng vi icon
});

let userMarker = null;
let searchMarker = null;
let routingControl = null;
// Bin  lu tr cc marker kt xe
let trafficJamMarkers = [];
let markers = []; // Mng cha cc marker
let currentLatitude = null;
let currentLongitude = null;

// Hm ly v tr hin ti ca ngi dng v t marker
function updatePosition(centerMap = true) {
    if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(
            function (position) {
                const lat = position.coords.latitude;
                const lon = position.coords.longitude;

                // t li marker ngi dng
                if (userMarker) {
                    map.removeLayer(userMarker);
                }

                userMarker = L.marker([lat, lon], { icon: customIcon }).addTo(map)
                    .bindPopup(`Bn ang  y!`)
                    .openPopup();

                // Ch t li v tr trung tm ca bn  khi centerMap l true
                if (centerMap) {
                    map.setView([lat, lon], 18);
                }

                console.log(`Cp nht v tr: Latitude ${lat}, Longitude ${lon}`);
            },
            function (error) {
                console.error("Li ly v tr ngi dng | M li:", error.message);
            }
        );
    } else {
        alert("Trnh duyt ca bn khng h tr Geolocation.");
    }
}

// Gi hm mi 10 giy  cp nht v tr ngi dng
setInterval(() => updatePosition(false), 10000); // Cp nht v tr mi 10 giy nhng khng thay i trung tm bn 

// Gi hm ban u vi centerMap = true  t trung tm khi ng dng khi ng
updatePosition(true);

// Gn s kin click cho nt "Tm v tr"
document.getElementById('trackButton').addEventListener('click', function () {
    if (userMarker) {
        map.removeLayer(userMarker); // Xa marker c nu tn ti
        userMarker = null;
    }
    updatePosition(true); // Cp nht v tr v t trung tm bn 

    // Xa marker tm kim nu tn ti
    if (searchMarker) {
        map.removeLayer(searchMarker);
        searchMarker = null;
    }
});
// Gn s kin click cho nt "Hin th iu hng"
document.getElementById('routingButton').addEventListener('click', function () {
    if (routingControl) {
        map.removeControl(routingControl); // n iu hng nu n ang hin th
        routingControl = null;
    } else {
        if (navigator.geolocation) {
            navigator.geolocation.getCurrentPosition(function (position) {
                const currentLatLng = L.latLng(position.coords.latitude, position.coords.longitude);
                routingControl = L.Routing.control({
                    waypoints: [
                        currentLatLng, // V tr hin ti ca ngi dng
                    ],
                    routeWhileDragging: true,
                    geocoder: L.Control.Geocoder.nominatim(),
                    createMarker: function(i, waypoint, n) {
                        return L.marker(waypoint.latLng, {
                            icon: customGPS // S dng biu tng ty chnh
                        });
                    }
                }).addTo(map);
            }.bind(this), function (error) {
                console.error("Li ly v tr ngi dng | M li:", error);
                alert("Khng th ly v tr hin ti ca bn. Vui lng kim tra ci t v tr ca bn.");
            });
        } else {
            alert("Trnh duyt ca bn khng h tr Geolocation.");
        }
    }
});


// Thm thanh tm kim vo bn 
const provider = new OpenStreetMapProvider();

const searchControl = new GeoSearchControl({
    provider: provider,
    style: 'bar',
    animateZoom: true,
    keepResult: true,
    autoComplete: true,
    autoCompleteDelay: 250,
    showMarker: false, // Khng t ng hin th marker, s t qun l marker
    updateMap: true, // T ng cp nht v tr bn  khi tm kim
    keepResult: true, // Gi kt qu tm kim
    searchLabel: 'Bn ang tm kim g?...',
});

map.addControl(searchControl);

// Lng nghe s kin kt qu tm kim
map.on('geosearch/showlocation', function (event) {
    // Kim tra ta  hp l
    if (event.location && event.location.x && event.location.y) {
        // Chuyn i x, y thnh lat, lng
        const latlng = {
            lat: event.location.y,
            lng: event.location.x
        };

        // Xa marker tm kim hin ti nu tn ti
        if (searchMarker) {
            map.removeLayer(searchMarker);
        }

        // Thm marker mi cho v tr tm kim
        searchMarker = L.marker(latlng, { icon: customGPS }).addTo(map)
            .bindPopup(event.location.label)
            .openPopup();
    } else {
        console.error("Invalid search location data:", event.location);
    }
});
//////////////////////////////////////////////////////

// Hm  thm marker ti v tr kt xe
function addTrafficJamMarkers(trafficJams) {
    trafficJamMarkers.forEach(marker => map.removeLayer(marker));
    trafficJamMarkers = [];

    trafficJams.forEach(jam => {
        const markerColor = jam.severity === 'severe' ? 'red' : (jam.severity === 'moderate' ? 'orange' : 'yellow');
        const marker = L.circleMarker([jam.latitude, jam.longitude], {
            color: markerColor,
            fillColor: markerColor,
            fillOpacity: 0.8,
            radius: 10
        }).addTo(map);

        marker.bindPopup(`Mc : ${jam.severity}<br>Tc  TB: ${jam.avgSpeed.toFixed(2)} km/h<br>S thit b: ${jam.deviceCount}`);
        trafficJamMarkers.push(marker);
    });
}

// Lng nghe s kin kt ni thnh cng
socket.on('connect', () => {
    console.log('BE  kt ni');
  });

// Lng nghe s kin ngt kt ni
socket.on('disconnect', () => {
    console.log('BE  ngt kt ni');
  });
// Lng nghe s kin 'trafficJam' t server
socket.on('trafficJam', (trafficJams) => {
    console.log('Nhn d liu kt xe:', trafficJams);
    addTrafficJamMarkers(trafficJams);
});

socket.on('connect_error', (error) => {
    console.error('Li kt ni Socket.IO:', error);
});
    // Xa cc marker c trc khi thm mi  trnh chng lp
    markers.forEach(marker => map.removeLayer(marker));
    markers = [];
  
  
//////////////////////////////////////////////////////////////////
// darkmode
const themeToggle = document.getElementById('themeToggle');
const body = document.body;
const icon = themeToggle.querySelector('i');
themeToggle.addEventListener('click', () => {
    body.classList.toggle('dark-theme');
    if (body.classList.contains('dark-theme')) {
        icon.classList.remove('fa-moon');
        icon.classList.add('fa-sun');
        localStorage.setItem('theme', 'dark');
    } else {
        icon.classList.remove('fa-sun');
        icon.classList.add('fa-moon');
        localStorage.setItem('theme', 'light');
    }
});
// Example function to update the layout
function updateLayout() {
    const geoSearchControlContainer = document.querySelector('.leaflet-control-geosearch');
    const routingContainer = document.querySelector('.leaflet-routing-container');

    if (geoSearchControlContainer && routingContainer) {
        // Check if routing container is visible
        const routingVisible = routingContainer.style.display !== 'none';

        if (routingVisible) {
            geoSearchControlContainer.style.width = 'calc(100% - 20px)'; // Adjust width if routing is visible
        } else {
            geoSearchControlContainer.style.width = 'auto'; // Reset width if routing is not visible
        }
    }
}
//hm ngi dng bo co 
const reportButton = document.getElementById('reportButton');
reportButton.addEventListener('click', () => {
    if (navigator.geolocation) {
        // Ly v tr hin ti ca ngi dng
        navigator.geolocation.getCurrentPosition(position => {
            const reportData = {
                message: 'Traffic jam reported!',
                latitude: position.coords.latitude,  // Ly ta  v 
                longitude: position.coords.longitude,  // Ly ta  kinh 
                timestamp: new Date().toISOString()    // Thi gian bo co
            };
            // Gi d liu bo co kt xe n backend
            socket.emit('reportTraffic', reportData);
        }, error => {
            console.error("Li khi ly v tr:", error);
        });
    } else {
        console.error("Trnh duyt khng h tr geolocation");
    }
});

// Lng nghe thng bo ngi dng t backend
socket.on('trafficNotification', (data) => {
    console.log('Notification from server:', data);
});
//cu hnh kt xe
socket.on('updatedTrafficConfig', (newConfig) => {
    console.log('cu hnh kt xe  c cp nht:', newConfig);
});
// Event listener for routing state changes
map.on('routing:start', updateLayout);
map.on('routing:end', updateLayout);

// Initial layout update
updateLayout();
